import axios from "axios";
import { API_URL } from "../config/constants";

const JWT_EXPIRY_TIME = 3600 * 1000;
const initialState = {
    refTok: false,
    login: false,
};

export const setLogin = (cookies) => ({type: "LOGIN", cookies: cookies});
export const setLogout = () => ({type: "LOGOUT"});
export const setRefresh = () => ({type: "TOKENREFRESH"});

export default function authentication(state = initialState, action) {
    switch (action.type) {
        case "LOGIN":
            onLoginSuccess(action.cookies.accessToken);
            return {
                ...state,
                refTok: true,
                login: true,
            }
        case "LOGOUT":
            return {
                ...state,
                refTok: false,
                login: false,
            }
        case "TOKENREFRESH":
            if(state.refTok) {
                onSilentRefresh();
            return state;
            } else {
                return state;
            }
        default:
            return state;
    }
};

const onSilentRefresh = () => {
    axios.post(`${API_URL}/silent-refresh`)
    .then((res) => {
        onLoginSuccess(res.data);
        console.log('  ')
    })
    .catch(error => {
        //    .  .
        console.log('  :', error.response.data.error);
    })
}
const onLoginSuccess = (accessToken) => {
    // accessToken 
    axios.defaults.headers.common['Authorization'] = `Bearer ${accessToken}`;
    // accessToken  1    
    setTimeout(onSilentRefresh, JWT_EXPIRY_TIME - 60000);
    // setTimeout(onSilentRefresh, 1000);
}